.menu-icon {
    height: 30px;
    width: 30px;
    position: absolute;
    z-index: 999;
    right: 50px;
    left: auto;
    top: 50px;
    cursor: pointer;
  }
  .menu-icon__line {
    height: 2px;
    width: 30px;
    display: block;
    background-color: #fff;
    margin-bottom: 7px;
    transition: transform 0.2s ease, background-color 0.5s ease;
    -moz-transition: color 0.2s 0.02s ease;
    -o-transition: color 0.2s 0.02s ease;
    -webkit-transition: color 0.2s 0.02s ease;
    -webkit-transition: All 0.4s ease;
    -moz-transition: All 0.4s ease;
    -o-transition: All 0.4s ease;
    -ms-transition: All 0.4s ease;
    transition: All 0.4s ease;
  }
  .menu-icon__line-left {
    width: 15px;
    margin-top: 5px;
  }
  .menu-icon__line-right {
    width: 15px;
    float: right;
  }
  .menu-icon:hover .menu-icon__line-left, .menu-icon:hover .menu-icon__line-right {
    width: 25px;
  }
.nav {
    position: fixed;
    z-index: 0;
  }
  body.nav-active .nav {
    z-index: 777;
  }
  body.nav-active .menu-icon__line-left {
    margin-top: 8px;
  }
  .nav:before, .nav:after {
    content: "";
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: rgba(234, 234, 234, 0.2);
    background: rgba(255, 255, 255, 0.15);
    z-index: -1;
    transition: transform cubic-bezier(0.77, 0, 0.175, 1) 0.8s;
    transform: translateX(0%) translateY(-100%);
  }
  .nav:after {
    background: #fff;
    transition-delay: 0s;
    background: url(/src/assets/images/white-fon5.jpg) center center repeat;
    background-size: auto auto;
  }
  .nav:before {
    transition-delay: 0.1s;
  }
  .nav__content {
    position: fixed;
    top: 50%;
    transform: translate(0%, -50%);
    width: 100%;
    text-align: center;
    font-size: calc(2vw + 10px);
    font-weight: 200;
    cursor: pointer;
  }
  .nav__list {
    width: 300px;
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
    padding: 0;
  }
  .nav__list ul, .nav__list div {
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
  }
  .nav__list-item, .nav__list ul li {
    position: relative;
    display: inline-block;
    transition-delay: 0.8s;
    opacity: 0;
    transform: translate(0%, 100%);
    transition: opacity 0.2s ease, transform 0.3s ease;
    margin-right: 25px;
    font-size: 42px;
    line-height: 52px;
    font-family: HanZi;
    width: 100%;
    margin: 10px 0 !important;
  }
  .nav__list-item a {
    font-size: 52px;
    line-height: 62px;
    font-family: "YanoneKaffeesatzR";
    color: #000;
  }
  .nav__list-item:hover a {
    color: #ff131d;
  }
  .nav__list-item:before {
    content: "";
    position: absolute;
    background: #ff131d;
    width: 25px;
    height: 2.5px;
    top: 110%;
    transform: translate(0%, 0%);
    transition: all 0.3s ease;
    z-index: -1;
  }
  .nav__list-item:nth-child(1):hover:before {
    width: 129px;
  }
  .nav__list-item:nth-child(2):hover:before {
    width: 123px;
  }
  .nav__list-item:nth-child(3):hover:before {
    width: 93px;
  }
  .nav__list-item:nth-child(4):hover:before {
    width: 81px;
  }
  .nav__list-item:nth-child(5):hover:before {
    width: 124px;
  }
  body.nav-active .menu-icon {
    position: fixed;
  }
  body.nav-active .menu-icon__line {
    background-color: #000;
    transform: translateX(0) rotate(-45deg);
    margin-bottom: 4px;
  }
  body.nav-active .menu-icon:hover .menu-icon__line {
    background-color: #ff131d !important;
  }
  body.nav-active .menu-icon:hover .menu-icon__line-left, body.nav-active .menu-icon:hover .menu-icon__line-right {
    width: 15px;
  }
  body.nav-active .menu-icon__line-left {
    transform: translateX(2px) rotate(45deg);
  }
  body.nav-active .menu-icon__line-right {
    transform: translateX(-2px) rotate(45deg);
    margin-top: -1px;
  }
  body.nav-active .nav {
    visibility: visible;
  }
  body.nav-active .nav:before, body.nav-active .nav:after {
    transform: translateX(0%) translateY(0%);
  }
  body.nav-active .nav:after {
    transition-delay: 0.1s;
  }
  body.nav-active .nav:before {
    transition-delay: 0s;
  }
  body.nav-active .nav__list-item {
    opacity: 1;
    transform: translateX(0%);
    transition: opacity 0.3s ease, transform 0.3s ease, color 0.3s ease;
  }
  body.nav-active .nav__list-item:last-of-type {
    margin-right: 0 !important;
  }
  body.nav-active .nav__list-item:nth-child(0) {
    transition-delay: 0.5s;
  }
  body.nav-active .nav__list-item:nth-child(1) {
    transition-delay: 0.6s;
  }
  body.nav-active .nav__list-item:nth-child(2) {
    transition-delay: 0.7s;
  }
  body.nav-active .nav__list-item:nth-child(3) {
    transition-delay: 0.8s;
  }
  body.nav-active .nav__list-item:nth-child(4) {
    transition-delay: 0.9s;
  }
  body.nav-active .nav__list-item:nth-child(5) {
    transition-delay: 1s;
  }
  body.nav-active .nav__list-item:nth-child(0):hover, body.nav-active .nav__list-item:nth-child(1):hover, body.nav-active .nav__list-item:nth-child(2):hover, body.nav-active .nav__list-item:nth-child(3):hover, body.nav-active .nav__list-item:nth-child(4):hover, body.nav-active .nav__list-item:nth-child(5):hover {
    transition-delay: 0s;
  }
